[hidden],
.hidden {
  display: none !important; }

[disabled],
.disabled {
  cursor: not-allowed; }

:focus {
  outline: 0;
  box-shadow: 0 0 0 2px #5bbce4, inset 0 0 0 2px #5bbce4; }

:focus:not(:focus-visible) {
  outline: 0; }

p {
  color: #111521; }

.button {
  padding: 1em .75em;
  position: relative;
  text-decoration: none;
  background: #3c81fd;
  transition: all 0.125s ease-out; }
  .button:focus, .button:hover, .button--active {
    background: #6209f8;
    transition: all 0.075s ease-out; }

.link {
  font-weight: 600;
  text-decoration: none;
  color: #ffffff;
  text-shadow: -0.08em -0.04em 0 #111521, 0.08em -0.04em 0 #111521, 0.08em 0.04em 0 #111521, -0.08em 0.04em 0 #111521;
  box-shadow: inset 0 -0.05em 0 0 #111521, inset 0 -0.175em 0 0 #ffffff;
