  .link:hover, .link:focus {
    box-shadow: inset 0 -0.05em 0 0 #111521, inset 0 -0.175em 0 0 #3c81fd;
    transition: box-shadow 0.075s ease-out; }

.block {
  opacity: 0;
  position: relative;
  background: transparent; }
  .block.visible {
    opacity: 1;
    animation: fromTop .5s ease-out forwards; }

