.banner {
  display: flex;
  flex-direction: column;
  background: #3c81fd;
  overflow: hidden; }
  @media (min-width: 600px) {
    .banner {
      flex-direction: row;
      align-items: stretch; } }
  .banner__section {
    position: relative; }
  @media (min-width: 600px) {
    .banner__branding {
      width: 45%; } }
  .banner__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0 7.5vw;
    animation: fromTop .75s ease-out forwards; }
    @media (min-width: 600px) {
      .banner__logo {
        animation: fromLeft .75s ease-out forwards; } }
  .banner__content {
    background: #ffffff; }
    @media (min-width: 600px) {
      .banner__content {
        width: 55%;
        transform: skewX(-15deg); }
        .banner__content:after {
          content: '';
          background: #ffffff;
          height: 100%;
          width: 50%;
          position: absolute;
          top: 0;
          right: 0;
          transform: skewX(15deg);
          z-index: -1; } }
  .banner__slogan {
    height: 100%;
