.pane {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 20vw 5vw; }
  @media (min-width: 600px) {
    .pane {
      padding: 15vw 10vw; } }
  @media (min-width: 900px) {
    .pane {
      padding: 10vw 20vw; } }
  .pane:nth-child(odd) {
    background: #3c81fd; }
  .pane:nth-child(even) {
    background: #27c2ff; }
  .pane--intro, .pane--outro {
    padding: 20vw 5vw;
    font-size: 5vw; }
    @media (min-width: 600px) {
      .pane--intro, .pane--outro {
        padding: 20vw 10vw;
        font-size: 3.5vw; } }
    @media (min-width: 900px) {
      .pane--intro, .pane--outro {
        padding: 20vw;
        font-size: 2.25vw; } }
  .pane__content {
    position: relative;
    z-index: 50;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center; }
    .pane__content--intro, .pane__content--outro {
      align-items: center; }
      @media (min-width: 900px) {
        .pane__content--intro, .pane__content--outro {
          flex-direction: row; } }
  .pane__body, .pane__next {
    margin: 2em 0 0; }
  .pane + .pane {
